.admin-framework-page {
  position: relative;
}
.admin-framework-page .page-content {
  position: relative;
  min-height: 600px;
  min-height: calc(100vh - 92px);
  overflow: auto;
}
.admin-breadcrumb {
  height: 42px;
  line-height: 42px;
  border-bottom: 1px solid #ddd;
  position: absolute;
  right: 12px;
  top: 0;
}



