.mini .admin-framework-header-brand {
  width: 60px;
  -webkit-transition: all .3s;
  -o-transition: all .3s;
  transition: all .3s;
}
li {
  list-style: none;
}
.admin-framework-header {
  background-color: #2573e8 !important;
  height: 50px;
  overflow: hidden;
}
.admin-framework-header-brand {
  float: left;
  height: 100%;
  width: 200px;
  -webkit-transition: all .3s;
  -o-transition: all .3s;
  transition: all .3s;
  overflow: hidden;
  text-align: center;
  color: #fff;
  line-height: 50px;
}
.admin-framework-header-brand .brand-logo {
  height: 30px;
  margin-top: 10px;
}
.admin-framework-header-sidebar-toggle {
  float: left;
}
.admin-framework-header-sidebar-toggle .toggle-icon {
  line-height: 50px;
  font-size: 20px;
}
.admin-framework-header-sidebar-toggle a {
  display: block;
  padding: 0 10px;
  color: #fff;
}
.admin-framework-header-sidebar-toggle a:hover {
  background-color: #1766dc !important;
  color: #fff;
}
.admin-framework-header-menu {
  float: right;
}
.admin-framework-header-menu .menu-item {
  float: left;
}
.admin-framework-header-menu .menu-item a {
  display: block;
  padding: 0 10px;
  line-height: 50px;
  color: #fff;
}
.admin-framework-header-menu .menu-item a:hover {
  background-color: #1766dc !important;
  color: #fff;
}
.admin-framework-header-menu .menu-item .header-menu-text {
  margin-left: 6px;
}
@media (max-width: 767px) {
  .header-menu-text {
    display: none !important;
  }
}



