.login-page {
  background: #f0f5f8;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.login-page .login-box {
  position: absolute;
  top: 45%;
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
      -ms-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
  width: 400px;
  padding: 30px;
  background-color: #fff;
  border-radius: 4px;
}
.login-page .login-box h2 {
  text-align: center;
  font-size: 24px;
  margin: 24px;
}
.login-page .login-box .logo {
  display: block;
  margin: 0 auto 24px;
  height: 80px;
}
.login-page .login-box .ant-input {
  height: 44px;
  border-radius: 0;
}
.login-page .login-box .ant-btn {
  height: 44px;
  width: 100%;
  border-radius: 0;
}
@media (max-width: 767px) {
  .login-page .login-box {
    background-color: transparent;
    width: 100%;
    padding: 10px;
  }
}



