.mini .admin-framework-sidebar {
  width: 61px;
  -webkit-transition: all .3s;
  -o-transition: all .3s;
  transition: all .3s;
}
.mini .ant-menu-root {
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
  width: 60px;
}
.mini .ant-menu-root > li > div {
  -webkit-transition: all .3s;
  -o-transition: all .3s;
  transition: all .3s;
  padding: 0px 16px 0 24px !important;
}
.mini .ant-menu-root > li > div:after {
  right: 6px;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}
.mini .ant-menu-root > li > div {
  overflow: hidden;
}
.mini .ant-menu-root > li > div > span > span {
  -webkit-transition: all .3s;
  -o-transition: all .3s;
  transition: all .3s;
  opacity: 0;
  width: 0;
}
.mini .ant-menu-root > li.ant-menu-item {
  -webkit-transition: all .3s;
  -o-transition: all .3s;
  transition: all .3s;
  padding: 0px 16px 0 24px !important;
}
.mini .ant-menu-root > li.ant-menu-item > a {
  overflow: hidden;
}
.mini .ant-menu-root > li.ant-menu-item > a > span {
  -webkit-transition: all .3s;
  -o-transition: all .3s;
  transition: all .3s;
  opacity: 0;
  width: 0;
}
.ant-menu-root {
  width: 200px;
  -webkit-transition: all .3s;
  -o-transition: all .3s;
  transition: all .3s;
}
.ant-menu-root > li > div > span > span {
  -webkit-transition: all .3s;
  -o-transition: all .3s;
  transition: all .3s;
}
.ant-menu-root > li.ant-menu-item > a > span {
  -webkit-transition: all .3s;
  -o-transition: all .3s;
  transition: all .3s;
}
.ant-menu-inline .ant-menu-item,
.ant-menu-vertical .ant-menu-item {
  border-right: 1px solid transparent;
}
.ant-menu-inline,
.ant-menu-vertical {
  border-right: 1px solid transparent;
}
.ant-menu-item-selected > a {
  overflow: hidden;
}
.admin-framework-sidebar {
  -webkit-transition: all .3s;
  -o-transition: all .3s;
  transition: all .3s;
  width: 201px;
  border-right: 1px solid #ddd;
  background-color: #fff;
  float: left;
  margin-right: -200px;
}
.admin-framework-sidebar .ant-menu-item {
  -webkit-transition: all .3s;
  -o-transition: all .3s;
  transition: all .3s;
  border-right: none;
}
.admin-framework-sidebar .ant-menu-item-selected {
  -webkit-transition: .3s;
  -o-transition: .3s;
  transition: .3s;
  border-right: 0;
  background-color: #eaf8fe;
}
.ant-menu-submenu-vertical {
  z-index: 999;
}
.admin-framework-sidebar .ant-menu .fa {
  margin-right: 8px;
  width: 12px;
  height: 12px;
  text-align: center;
}
.admin-framework-sidebar .ant-menu-submenu-selected.ant-menu-submenu {
  color: #108ee9;
}
@media (max-width: 767px) {
  .mini .admin-framework-sidebar {
    overflow: hidden;
    width: 0;
    -webkit-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
  }
  .mini .ant-menu-root {
    -webkit-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
    width: 0;
  }
  .mini .ant-menu-root > li > div,
  .mini .ant-menu-root > li.ant-menu-item {
    padding: 0 !important;
  }
}



