.itemContainer {
  min-height: 500px;
}
.itemContainer .avatar-uploader,
.itemContainer .avatar-uploader-trigger,
.itemContainer .avatar {
  width: 150px;
  height: 150px;
}
.itemContainer .avatar-uploader {
  display: block;
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
}
.itemContainer .avatar-uploader-trigger {
  display: table-cell;
  vertical-align: middle;
  font-size: 28px;
  color: #999;
}



